@import '../../global.scss';

.wrapperFooter {
    width: 100%;
    height: 80px;
    background-color: var(--bg-color);
    .containerFooter {
        background-color: var(--bg-color);
        height: 100%;
        width: calc(56% + 115px);
        margin-left:auto ;
        margin-right: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        .containerLinks {
            width: 80%;
            height: 40%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: left;
            position: absolute;
            left: 0;
            background-color: transparent;
            a {
                color: var(--second-text-color);
                text-decoration: none;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 35px;
                margin-right: 20px;
                font-size: 18px;
                #ghi {
                    font-size: 26px;
                    margin-right: 10px;
                    span {
                        margin-left: 10px;
                        margin-right: 10px;
                    }
                }
                #lii {
                    font-size: 30px;
                    margin-right: 10px;
                    margin-left: 10px;
                    span {
                        margin-left: 10px;
                        margin-right: 10px;
                    }
                }  
                #ei {
                    font-size: 32px;
                    margin-left: 10px;
                    margin-right: 10px;
                    span {
                        margin-left: 10px;
                        margin-right: 10px;
                    }
                }
                #fi{
                    font-size: 30px;
                    margin-left: 10px;
                    margin-right: 10px;
                    span{
                        margin-left: 10px;
                        margin-right: 10px;
                    }
                }
            }  
        }


        .containerColorMode {
            position: absolute;
            right: 55px;
            height: 40%;
            display: flex;
            flex-direction: row;
            align-items: center;
            span {
                height: 100%;
                display: flex;
                align-items: center;
                color: var(--second-text-color);
                margin-right: 20px;
                font-size: 18px;
            }
            #lmi {
                color: var(--main-accent-color);
                font-size: 31px;
            }

            #mni {
                color: var(--main-color);
                font-size: 28px;
            }
        }

        .containerVersionTag {
            position: absolute;
            right: 0;
            color: var(--second-text-color);
            margin-left: 10px;
        }
    } 
}

